const Gallery = props => (
  <div className="gallery">
    {props.images.map((image, index) => (
      <div className="tile">
        <img key={index} src={"/static/gallery/" + image} />
      </div>
    ))}

    <style jsx>{`
      .gallery {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: repeat(3, 5vw);
        grid-gap: 15px;
      }
      .tile {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    `}</style>
  </div>
);
export default Gallery;
